@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$negro;
    margin-top: 6rem;

    &__ul{
        color: v.$blanco;
        gap: 2rem;
        padding: 2rem;
        display: flex;
        justify-content: center;
    }

    &__a{
        font-size: 2.5rem;
        &:hover{
            color: v.$azul;
        }
    }

    &__p{
        color: v.$blanco;
        font-size: 2rem;
        text-align: center;
    }
}

