@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$negro;
    
    &__h1{
        color: v.$blanco;
        text-align: center;
        font-size: 3.4rem;
        @include m.tablet {
            margin: 0 ;
        }
    }

    &__span{
        color: v.$azul;
    }

    &__contenido{
        padding: 1rem;
        
        @include m.tablet{
            display:flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}



